import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, List, Cell} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import { Link } from '@vkontakte/vkui';
import './Main.css';

const osname = platform();

const Rules = props => (
  <Panel id={props.id}>
    <PanelHeader
      noShadow
      addon={<HeaderButton onClick={props.go} data-to="home"></HeaderButton>}
      left={<HeaderButton onClick={props.go} data-to="home">
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
      </HeaderButton>}
    >
      
    </PanelHeader>
    <Group>
        <List>
          <Cell>
                20.  ,       .     <nobr>78</nobr>.    .
          </Cell>
          <Cell>
                      Android iOS.
          </Cell>
          <Cell>
                  ,       .      .
          </Cell>
          <Cell>
                 : <strong>#BingoNewYearStreamers</strong>.
          </Cell>
          <Cell>
                 ,        .
          </Cell>
          <Cell>
              =  .    ,     .
          </Cell>
          <Cell>
                .    .
          </Cell>
          <Cell>
                 .
          </Cell>
          <Cell>
               20 ,   13 .
          </Cell>
          <Cell>
               , ,  <Link href="https://vk.com/authors"> </Link>.         
          </Cell>
        </List>
      </Group>
  </Panel>
);

Rules.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired
};

export default Rules;



